    heroRoles = (role) => {
      var roles = '';        
      for (var i = 0; i < role.length; i++) {
       if (i === role.length-1) {                
        roles = roles + role[i]
      } else {
        roles = roles + role[i] + ' - '
      }
    }
    return roles
  }

